.app {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  margin: 0 20px;
}

.cataloge {
  display: flex;
  flex-wrap: wrap;
  justify-content:center;
}

.catalogpage h2 {
  font-family: 'Montserrat', sans-serif;
  font-size: 20px;
  font-weight: 600;
  color: #838383;
}

.catalogpage {
  margin-bottom: 10px;
}

.shopping-cart {
  width: 100%;
  max-width: 700px;
}

.shopping-total {
  width: 100%;
  max-width: 340px;
}

.cart-page h1 {
  font-family: 'Montserrat', sans-serif;
  font-size: 20px;
  font-weight: 600;
  color: #1C1C27;
}

.cart-page-content {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}